<script>
import Message from '@/components/Message.vue'
import Common from '@/components/Common.vue';
import ChatGPT from '@/components/ChatGPT.vue';

export default {
  name:"ChatView",
  components: { Message,Common,ChatGPT },
  data(){
    return {
      conversation:[],
      inputText:"",
      headIcons:{
        chatGPT:null,
        user:null
      },
    }
  },
  created(){
    this.headIcons.assistant = require("@/assets/icon_chatGPT.svg");
    this.headIcons.user = Common.headIcon;
    this.conversation.push({role:"assistant",content:"Hello!I'm chatGPT."});
    this.conversation.push({role:"assistant",content:"What can I help you?"});
    ChatGPT.setConfig({
      apiUrl:Common.apiUrl,
      apiKey:Common.apiKey,
      model:"gpt-3.5-turbo",
      temperature:0.5,
      max_tokens:200,
      stop:"\n"
    });
  },
  methods: {
    sendMessage(){
      if(this.inputText.length == 0)
        return;
      ChatGPT.post(this.inputText,this.getResponse);
      this.conversation.push({role:"user",content:this.inputText});
      this.inputText = "";
      this.$nextTick(() => {
        // 获取到需要滚动的div元素
        const scrollDiv = this.$refs.scrollDiv;
        // 将滚动条滚动到底部
        scrollDiv.scrollTop = scrollDiv.scrollHeight;
      })
    },
    getResponse(data){
      this.conversation.push(data.choices[0].message);
      this.$nextTick(() => {
        // 获取到需要滚动的div元素
        const scrollDiv = this.$refs.scrollDiv;
        // 将滚动条滚动到底部
        scrollDiv.scrollTop = scrollDiv.scrollHeight;
      })
    }
  },
}
</script>

<template>
  <div class="mainContent">
    <div class="conversation" ref="scrollDiv">
      <Message v-for="message in conversation" 
        :headIcon="headIcons[message.role]" :msg="message.content" :sender="message.role"></Message>
    </div>
    <div class="messageInput">
      <textarea class="inputBox" v-model="inputText" @keydown.ctrl.enter="sendMessage" rows="1" onscroll="if(this.rows<4) this.rows++;"/>
      <button class="button" @click="sendMessage">发送</button>
    </div>
  </div>
</template>

