  height: 400px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  padding:1vh 2vw 1vh 2vw;
}
.conversation {
  flex-grow: 1;
  overflow-y: auto;
}
